import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import FlipCountdown from '@rumess/react-flip-countdown';
import blockexplorer from 'blockchain.info/blockexplorer';
import statistics from 'blockchain.info/statistics';

let blockID = "1L68X1wuCgAFr2YUs8n83TPkbB69LMvayK"

let options = {
  timespan:'all',
  rollingAverage:'24h'
}

class App extends Component {

  constructor(props){
    super(props)

    this.state ={
      dollarPrice:{},
      blockInfo:{},
      lastAddress:""
    }
  
  }
  componentWillMount(){

    statistics.get(options).then((result)=>{
      console.log("result",result)
      this.setState({
        dollarPrice:result.market_price_usd
      })
    })
    
    blockexplorer.getBalance(blockID,options).then((result)=>{
      console.log("result 2",result)
      this.setState({
        blockInfo: (result[blockID].final_balance / 100000000) 
      })
    })

    blockexplorer.getAddress(blockID,{}).then((result)=>{
      console.log("result 3",result)
      let inputs = result.txs[result.txs.length -1].inputs
      let lastInput = inputs[inputs.length -1].prev_out.addr
      this.setState({
        lastAddress:lastInput
      })
    })
  }
  render() {
    

    return (
      <div className="App">
        <div className="App-header">
    
          <h2>The last wallet to donate (mininum 0.01 BTC) will recieve 80% of this wallet's content!</h2>
          <h3>
            Total wallent content : {(this.state.blockInfo *1).toFixed(5)} BTC 
          </h3>
        </div>
      
        <div style={{margin:"25px"}}>
        <a href={"https://www.blockchain.com/btc/address/"+blockID} target="_blank" style={{  wordBreak: "break-all"}}>
          <svg shape-rendering="crispEdges" height="200" width="200" viewBox="0 0 29 29" class="qrcode"><path fill="#FFFFFF" d="M0,0 h29v29H0z"></path><path fill="#000000" d="M0 0h7v1H0zM8 0h1v1H8zM10 0h1v1H10zM12 0h2v1H12zM15 0h3v1H15zM20 0h1v1H20zM22,0 h7v1H22zM0 1h1v1H0zM6 1h1v1H6zM9 1h4v1H9zM16 1h2v1H16zM20 1h1v1H20zM22 1h1v1H22zM28,1 h1v1H28zM0 2h1v1H0zM2 2h3v1H2zM6 2h1v1H6zM8 2h3v1H8zM13 2h1v1H13zM16 2h1v1H16zM18 2h1v1H18zM22 2h1v1H22zM24 2h3v1H24zM28,2 h1v1H28zM0 3h1v1H0zM2 3h3v1H2zM6 3h1v1H6zM8 3h3v1H8zM12 3h1v1H12zM14 3h1v1H14zM16 3h1v1H16zM20 3h1v1H20zM22 3h1v1H22zM24 3h3v1H24zM28,3 h1v1H28zM0 4h1v1H0zM2 4h3v1H2zM6 4h1v1H6zM8 4h1v1H8zM11 4h5v1H11zM17 4h4v1H17zM22 4h1v1H22zM24 4h3v1H24zM28,4 h1v1H28zM0 5h1v1H0zM6 5h1v1H6zM10 5h2v1H10zM13 5h2v1H13zM16 5h1v1H16zM18 5h2v1H18zM22 5h1v1H22zM28,5 h1v1H28zM0 6h7v1H0zM8 6h1v1H8zM10 6h1v1H10zM12 6h1v1H12zM14 6h1v1H14zM16 6h1v1H16zM18 6h1v1H18zM20 6h1v1H20zM22,6 h7v1H22zM10 7h1v1H10zM12 7h1v1H12zM15 7h6v1H15zM0 8h4v1H0zM6 8h1v1H6zM8 8h1v1H8zM10 8h3v1H10zM14 8h1v1H14zM17 8h5v1H17zM24 8h3v1H24zM28,8 h1v1H28zM0 9h6v1H0zM8 9h1v1H8zM10 9h1v1H10zM12 9h1v1H12zM14 9h1v1H14zM19 9h2v1H19zM23 9h2v1H23zM27,9 h2v1H27zM2 10h3v1H2zM6 10h8v1H6zM20 10h1v1H20zM22 10h2v1H22zM25 10h1v1H25zM27 10h1v1H27zM2 11h2v1H2zM9 11h2v1H9zM13 11h1v1H13zM15 11h2v1H15zM18 11h1v1H18zM20 11h1v1H20zM22 11h1v1H22zM24 11h1v1H24zM28,11 h1v1H28zM3 12h8v1H3zM12 12h4v1H12zM17 12h2v1H17zM21 12h1v1H21zM23 12h1v1H23zM26 12h1v1H26zM1 13h3v1H1zM8 13h2v1H8zM11 13h2v1H11zM14 13h1v1H14zM16 13h1v1H16zM18 13h1v1H18zM21 13h3v1H21zM25 13h1v1H25zM27,13 h2v1H27zM1 14h2v1H1zM5 14h7v1H5zM13 14h1v1H13zM16 14h1v1H16zM19 14h2v1H19zM22 14h1v1H22zM24 14h1v1H24zM26,14 h3v1H26zM0 15h1v1H0zM2 15h1v1H2zM5 15h1v1H5zM7 15h1v1H7zM9 15h1v1H9zM11 15h1v1H11zM14 15h1v1H14zM18 15h1v1H18zM23 15h3v1H23zM0 16h1v1H0zM4 16h3v1H4zM8 16h1v1H8zM10 16h1v1H10zM12 16h2v1H12zM18 16h1v1H18zM20 16h2v1H20zM24 16h1v1H24zM28,16 h1v1H28zM4 17h2v1H4zM8 17h1v1H8zM10 17h1v1H10zM14 17h2v1H14zM17 17h1v1H17zM19 17h3v1H19zM25 17h2v1H25zM0 18h1v1H0zM2 18h1v1H2zM5 18h6v1H5zM15 18h3v1H15zM20 18h1v1H20zM24 18h2v1H24zM2 19h1v1H2zM7 19h2v1H7zM10 19h2v1H10zM15 19h1v1H15zM17 19h1v1H17zM19 19h1v1H19zM21 19h2v1H21zM24 19h3v1H24zM28,19 h1v1H28zM1 20h1v1H1zM4 20h1v1H4zM6 20h4v1H6zM13 20h5v1H13zM19 20h8v1H19zM28,20 h1v1H28zM8 21h1v1H8zM13 21h1v1H13zM16 21h2v1H16zM20 21h1v1H20zM24,21 h5v1H24zM0 22h7v1H0zM9 22h1v1H9zM13 22h2v1H13zM17 22h1v1H17zM19 22h2v1H19zM22 22h1v1H22zM24 22h1v1H24zM27 22h1v1H27zM0 23h1v1H0zM6 23h1v1H6zM9 23h6v1H9zM16 23h1v1H16zM20 23h1v1H20zM24 23h2v1H24zM0 24h1v1H0zM2 24h3v1H2zM6 24h1v1H6zM9 24h3v1H9zM15 24h4v1H15zM20,24 h9v1H20zM0 25h1v1H0zM2 25h3v1H2zM6 25h1v1H6zM8 25h1v1H8zM11 25h1v1H11zM13 25h2v1H13zM16 25h4v1H16zM21 25h1v1H21zM23 25h1v1H23zM28,25 h1v1H28zM0 26h1v1H0zM2 26h3v1H2zM6 26h1v1H6zM8 26h2v1H8zM12 26h1v1H12zM14 26h1v1H14zM16 26h2v1H16zM19 26h1v1H19zM21 26h1v1H21zM23 26h1v1H23zM25 26h2v1H25zM28,26 h1v1H28zM0 27h1v1H0zM6 27h1v1H6zM8 27h2v1H8zM12 27h2v1H12zM15 27h2v1H15zM23 27h2v1H23zM27 27h1v1H27zM0 28h7v1H0zM8 28h2v1H8zM11 28h1v1H11zM16 28h6v1H16zM23 28h1v1H23zM25 28h1v1H25zM27 28h1v1H27z"></path></svg>
         <br />
         <br />
          {"https://www.blockchain.com/btc/address/"+blockID}
        </a>
        </div>
        <div  style={{margin:"25px"}}>

        <h1>
        The winner gets :
        </h1> 
        <h2>
        80% of {(this.state.blockInfo *1).toFixed(5)} BTC for {(this.state.blockInfo * 0.8).toFixed(5)} BTC
        </h2>
       
        { /*
        <h3>That equals approximately : </h3>

        <h2>
        ${(0.8*(this.state.dollarPrice * this.state.blockInfo )).toFixed(2)} USD
        </h2>
        */}
        </div>
      
        <FlipCountdown
          hideYear
          hideMonth
          hideDay
          /*hideSecond*/
          theme='light' // Options (Default: dark): dark, light.
          endAt={'2022-07-19 12:00:00'} // Date/Time
        />

        <br />
        <h2  style={{margin:20}}>
          Last address to donate (current winner) confirmed by blockchain.com : <a  style={{  wordBreak: "break-all"}} target="_blank" href={"https://www.blockchain.com/btc/address/"+this.state.lastAddress}>{this.state.lastAddress}</a>. 
        </h2>
        <h3  style={{margin:20}}>
        The prize will transfered to the last wallet address that transfers any amount of currency of at least 0.01 BTC to this wallet address <a  style={{  wordBreak: "break-all"}} href={"https://www.blockchain.com/btc/address/"+blockID}target="_blank" >{blockID}</a> before this countdown restarts.
        </h3> 
        <div style={{margin:20, textAlign:"left"}}>
         Refer to <a href={"https://www.blockchain.com/stats"} target="_blank">blockchain.com/stats</a> for more information.
        </div>
        { /*
        <div style={{margin:20, textAlign:"left"}}>
          1 BTC = ${(this.state.dollarPrice*1).toFixed(2)} USD according to <a href={"https://www.blockchain.com/stats"} target="_blank">blockchain.com/stats</a> and therefore {(this.state.blockInfo *0.8).toFixed(5)} BTC is quoted to be worth {(this.state.dollarPrice * this.state.blockInfo*0.8).toFixed(2)} USD.
        </div>
         */}
      </div>
    );
  }
}

export default App;



